.App {
  text-align: center;
}

 .header {
  background-color: #282c34;
  color: white;
  display: grid;
  grid-template-columns: 2fr 1fr;
}

.gameboard {
  display: grid;
  background-color: aliceblue;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(2, 1fr);
}
.card {
  background-color: aquamarine;
  margin: 10px;
}
